<template>
    <div class="search-bar">
      <!-- <i onClick="javascript:history.back(-1)"  class="iconfont back">&#xe640;</i> -->
      <img src="./images/shop_icon.png" alt="">
      <div @click="toSearch" class="input-wrapper">
        <i class="iconfont">&#xe628;</i>
        <span>输入你喜欢的商品名称</span>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'search-bar',
    methods:{
      toSearch(){
        this.$emit('toSearch')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/variable.styl"
@import "../../common/stylus/iconfont.styl"
@import "../../common/stylus/mixin.styl"
  .search-bar
    line-height $top-bar-height
    width 100%
    height $top-bar-height
    background-color #fff
    padding-left 0.5rem
    color #ababab
    font-size 0
    box-sizing border-box
    .back
      extend-click()
      font-size 0.875rem
      vertical-align middle
      color #000
    img
      vertical-align middle
      width 2.375rem
      margin-left 0.5rem
    .input-wrapper
      vertical-align middle
      display inline-block
      width 76.6%
      margin-left 0.531rem
      height 2rem
      line-height 2rem
      background-color #f5f5f5
      border-radius 1rem
      span
        font-size 0.75rem
      i
        font-size 0.75rem
        padding 0 0.5rem 0 1.031rem
</style>



