import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 好物推荐
* */
export function recommendGoods(opts) {
  return axios.post(CONFIG.API.RECOMMEND_GOODS, qs.stringify({
    pageIndex:opts.currentPage,
    pageSize:opts.pageSize
  })).then(res => res.data)
}
/*
* navlist
* */
export function navList(opts) {
  return axios.post(CONFIG.API.NAV_LIST).then(res => res.data)
}
/*
* navlist
* */
export function banner() {
  return axios.post(CONFIG.API.BANNER, qs.stringify({
    pageKey:'new_shop_index'
  })).then(res => res.data)
}

/*
* navlist
* */
export function shopCount() {
  return axios.post(CONFIG.API.SHOP_COUNT).then(res => res.data)
}



