<template>
    <div @click="handleItem" class="nav-item">
      <img :src="data.image" alt="">
      <p>{{data.title}}</p>
    </div>
</template>

<script>
  export default {
    name: 'nav-item',
    props:{
      data:{
        type:Object,
        default:null
      }
    },
    methods:{
      handleItem(){
        this.$emit('handleItem',{item:this.data})
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .nav-item
    width 100%
    text-align center
    img
      display inline-block
      width 2.813rem
      height 2.813rem
    p
      padding-top 0.688rem
      color #666
      font-size 0.75rem
</style>



