<template>
  <div class="home">
    <search-bar class="search-bar" @toSearch="toSearch"></search-bar>
    <scroll :pullup="true"
            @scrollToEnd="loadMore"
            :data="list" class="home-scroll" ref="homeScroll">
      <div>
        <div @click="know" class="know">购物须知</div>
        <img style="width: 100%" @load="loadImage"
             :src="banner"
             alt="">
        <div class="nav-wrapper" v-show="navList.length">
          <nav-item @handleItem="handleItem" v-for="item in navList"
                    :key="item.id" :data="item" class="nav-item"></nav-item>
        </div>

        <div class="list-wrapper">
          <div v-show="list.length>0" class="list-title">
            好物推荐
          </div>
          <div class="list">
            <goods-item @toDetail="toDetail" @loadImage="loadImage"
                        v-for="item in list" :key="item.id" class="list-item"
                        :data="item">
            </goods-item>
          </div>
        </div>
        <loading v-show="showLoad" class="loading"
                 :title="loadingTitle"></loading>
      </div>
    </scroll>
    <div @click="toShopCar" class="shop-car">
      <div v-show="num<10" class="num">{{num}}</div>
      <div v-show="num>9" class="num1">{{num}}</div>
    </div>
  </div>
</template>

<script>
  import tool from '../../common/js/util'
  import * as core from '../../api/home.js'
  import SearchBar from '../../base/search-bar/search-bar.vue'
  import Scroll from '../../base/scroll/scroll.vue'
  import NavItem from '../../base/nav-item/nav-item.vue'
  import GoodsItem from '../../base/goods-item/goods-item.vue'
  import Loading from '../../base/loading/loading'

  export default {
    name: 'Home',
    data() {
      return {
        banner: '',
        list: [],
        navList: [],
        loadingTitle: '正在加载...',
        showLoad: true,
        currentPage: 1,
        pageSize: 10,
        num: 0
      }
    },
    components: {
      SearchBar,
      Scroll,
      NavItem,
      GoodsItem,
      Loading
    },
    activated() {
      this.getShopCount()
    },
    created() {
      this.getShopCount()
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      this.getRecommendGoods({
        currentPage: this.currentPage,
        pageSize: this.pageSize
      });
      // this.getNavList();
      this.getBanner()
    },
    methods: {
      getShopCount() {
        core.shopCount().then(res => {
          if (res.code && '00' === res.code) {
            this.num = res.result.count
            console.log(res, 'dasdasdsadass')
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      know() {
        this.$router.push({path: '/know'})
      },
      toShopCar() {
        this.$router.push({path: '/shopCar'})
      },
      toDetail(e) {
        this.$router.push({path: '/commodityDetails', query: {commodityId: e}})
      },
      handleItem(e) {
        window.location.href = e.item.linkUrl
        //this.$router.push({path:'/classify',query:{categoryId:e.item.id}})
      },
      //滑动到底部
      loadMore() {
        if (!this.showLoad && !this.hasMore) {
          this.getRecommendGoods({
            currentPage: this.currentPage,
            pageSize: this.pageSize
          })
        }
      },
      getBanner() {
        core.banner().then(res => {
          if (res.code && '00' === res.code) {
            console.log(res)
            this.banner = res.result.moduleMap.mod_banner.dataList[0].image
            this.navList = res.result.moduleMap.mod_pindao.dataList;

          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      // getNavList(){
      //   core.navList().then(res=>{
      //     if (res.code&&'00'===res.code){
      //     this.navList=res.result;
      //     }
      //   })
      // },
      getRecommendGoods(opts) {
        this.showLoad = true
        core.recommendGoods(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.list = this.list.concat(res.result.data)
            this.loadingTitle = '';
            this.showLoad = false;
            this.currentPage++;
            if (this.list.length >= res.result.amount) {
              this.hasMore = true
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      toSearch() {
        this.$router.push({path: '/search'})
      },
      loadImage() {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$refs.homeScroll.refresh()
        }, 500)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/variable.styl"
  .home
    .shop-car
      position fixed
      z-index 999
      right 0.719rem
      bottom 4rem
      width 3.125rem
      height 3.125rem
      background url("../../common/images/shop_car.png") no-repeat 100% / 100%
      .num, .num1
        font-size 0.75rem
        color #fff
        position absolute
        right 0
        min-width 1rem
        height 1rem
        border-radius 0.5rem
        text-align center
        line-height 1rem
        background-color #ff1111
        top 0
      .num1
        padding 0 3px
    .loading
      padding 1rem 0
    .search-bar
      position absolute
      z-index 999
    .home-scroll
      position fixed
      background-color #f5f5f5
      top $top-bar-height
      bottom 0
      left 0
      right 0
      .know
        position absolute
        right 0
        top 1rem
        padding 0.25rem 0.75rem 0.25rem 0.75rem
        border-radius: 0.625rem 0rem 0rem 0.625rem;
        font-size 0.75rem
        color #fff
        background-color rgba(0, 0, 0, 0.5)
      .nav-wrapper
        display flex
        background-color #fff
        flex-wrap wrap
        justify-content left
        padding-bottom 1.219rem
        .nav-item
          width 25%
          padding-top 0.875rem
      .list-wrapper
        width 100%
        padding 0 3.2%
        box-sizing border-box
        margin 0 auto
        .list-title
          font-weight bold
          font-size 1.125rem
          color #212121
          padding 1.125rem 0 0.75rem 0
        .list
          /*column-gap 0.25rem*/
          /*column-count 2*/
          display flex
          justify-content space-between
          flex-wrap wrap
          padding-bottom 1rem
          .list-item
            width 49.43%
            overflow hidden
            border-radius 0.25rem
            margin-bottom 0.25rem
            break-inside: avoid;
</style>



